// Copied from the material ui docs and modified
import React from "react";

// import it with a different name, prevents a clash with my file name
import { Modal as MuiModal } from "@material-ui/core";

// modal is imported to the app level and controlled by state
// is used to house the create and delete forms

// import the modal contents








const Modal = (props) => {
  // getModalStyle is not a pure function, we roll the style only on the first render

  // set the content to be the add form by default
  let content = (
    <h1 id="modal">Testing....</h1>
  );
  

  // if its delete set it
  if (props.modalState[1] === "test") {
    content = (
      <h1>testig</h1>
    );
  }

  // modalcontrol with no params sets it to closed
  const setClose = () => {
    props.modalControl();
  };

  return (
    <div>
      <MuiModal
        open={props.modalState[0]}
        onClose={setClose}
        aria-labelledby="Add Modal"
        aria-describedby="Provides an add person form"
      >
        {content}
      </MuiModal>
    </div>
  );
};

Modal.defaultProps = {};

export default Modal;
