import { AppBar, Grid, makeStyles } from "@material-ui/core";
import { Search } from "@material-ui/icons";

import React, { useState, useEffect } from "react";

import BackBtn from "./BackBtn";
import SearchBtn from "./SearchBtn";

// only mount the button for the modal not the modal itself
// position static, sets it as a regular dom element

const useStyles = makeStyles({
  root: {
    height: "fit-content",
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
});

function Header(props) {
  const [searchState, setsearchState] = useState(false);
  const classes = useStyles();

  return (
    <AppBar color="primary" position="static" className={classes.root}>
      <BackBtn
        bodyState={props.bodyState}
        feedControl={props.feedControl}
        chatState={props.chatState}
        chatControl={props.chatControl}
        feedState={props.feedState}
      />
      <SearchBtn bodyControl={props.bodyControl} searchControl={props.searchControl}/>
    </AppBar>
  );
}

export default Header;
