import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";

import { ChatBubble, PlaylistAddCheck, Settings, Person } from "@material-ui/icons";

const useStyles = makeStyles({
  root: {
    width: "100%",
    position : "fixed",
    bottom : 0,
  },
});

// get the feedcontroller and modal controller from the app
// set the onchange toe handle the change of state

export default function BottomNAv(props) {
  const classes = useStyles();

  const handleChange = (event, newValue) => {
    if (newValue == "Menu") {
      props.modalControl(true , "Menu")
    } else {
      props.bodyControl(newValue);
    }
  };

  return (
    <BottomNavigation
      value={props.bodyState}
      onChange={handleChange}
      className={classes.root}
    >
      <BottomNavigationAction
        label="Feed"
        value="Feed"
        icon={<PlaylistAddCheck />}
      />
      <BottomNavigationAction
        label="Chats"
        value="Chats"
        icon={<ChatBubble />}
      />
      <BottomNavigationAction
        label="Account"
        value="Account"
        icon={<Person />}
      />
      <BottomNavigationAction label="Menu" value="Menu" icon={<Settings />} />
    </BottomNavigation>
  );
}
